
.movie-content-wrap {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
.top-bar-item {
  position: relative;
}
.selected label:after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -.08rem;
  width: 0.16rem;
  height: 0.16rem;
  margin-left: .02rem;
  border: 1px solid #e5e5e5;
  -webkit-transform: rotate(-45deg) translate(-50%,-50%) scale(.5);
  transform: rotate(-45deg) translate(-50%,-50%) scale(.5);
  border-bottom: none;
  border-left: none;
  background: #fff;
}



