
.search-wrap {
  height: 50px;
}
.search-wrap label,
.search-wrap i {
  font-size: .12rem;
}
.search-wrap .location {
  margin: 0 .2rem;
  line-height: .34rem;
}
.search-wrap .location i {
  color: #999;
}
.search-wrap form {
  height: .34rem;
}
.search-wrap form i {
  line-height: .34rem !important;
}
.search-wrap form input {
  height: 100% !important;
  padding: 0 !important;
}
.search-wrap input,
.search-wrap span,
.search-wrap a {
  font-size: .12rem !important;
  line-height: .34rem;
}



